@import "https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap";
html {
  height: 100vh;
}

.container {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.container > div {
  justify-content: center;
  align-items: center;
  padding: 50px 60px;
  display: flex;
}

.brand {
  padding: 50px;
}

.weather-app {
  min-height: 100vh;
  color: #fff;
  opacity: 1;
  background-image: url("cloudy.fef6f546.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all .5s;
  position: relative;
}

.weather-app:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 0;
  background: rgba(0, 0, 0, .3);
  position: absolute;
  top: 0;
  left: 0;
}

.city-time, .temp, .weather {
  margin: 0 1em;
}

.city-time h1 {
  margin: 0 0 .2em;
  font-size: 3em;
}

.temp {
  margin: 0;
  font-size: 6.5em;
}

.weather img {
  margin: .5em 0;
  display: block;
}

.panel {
  width: 30%;
  height: 100%;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  z-index: 1;
  background: rgba(110, 110, 110, .25);
  border: 1px solid rgba(255, 255, 255, .18);
  padding-left: 40px;
  padding-right: 40px;
  position: absolute;
  top: 0;
  right: 0;
  overflow-y: scroll;
  box-shadow: 0 8px 32px rgba(0, 0, 0, .3);
}

.panel form {
  margin-top: 3em;
}

.submit {
  color: #fff;
  cursor: pointer;
  background: #fa6d1b;
  border: none;
  outline: none;
  margin: 0;
  padding: 1.5em;
  font-size: 1.2em;
  transition: all .4s;
  position: absolute;
  top: 0;
  right: 0;
}

.submit:hover {
  color: #000;
  background: #fff;
}

.search {
  width: 80%;
  color: #fff;
  background: none;
  border: none;
  border-bottom: 1px solid #ccc;
  padding: 0 1em .5em 0;
  font-size: 1.1em;
}

.search:focus {
  outline: none;
}

.search::-ms-input-placeholder {
  color: #ccc;
}

.search::placeholder {
  color: #ccc;
}

.panel ul {
  border-bottom: 1px solid #ccc;
  margin: 2em 0;
  padding: 0 0 1em;
}

.panel ul li {
  color: #ccc;
  margin: 2.5em 0;
  transition: color .25s ease-in-out;
}

.title {
  margin: 3em 0;
}

.city {
  cursor: pointer;
  display: block;
}

.city:hover {
  color: #fff;
}

.details li {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 855px) {
  .container {
    justify-content: space-between;
  }
}

@media screen and (max-width: 915px) {
  .temp {
    font-size: 3.5em;
  }
}

@media screen and (max-width: 800px) {
  .panel, .container {
    width: 100%;
    top: initial;
    position: relative;
  }

  .city-time h1 {
    font-size: 2em;
  }

  .brand {
    padding: 20px;
  }

  .container > div {
    margin: 0 auto 30px;
    padding: 50px 40px;
  }

  .submit {
    display: none;
  }

  .panel {
    width: 40%;
  }

  .panel ul li {
    margin: 2em 0;
    font-size: 14px;
  }
}

@media screen and (max-width: 500px) {
  html {
    font-size: 12px;
  }

  .temp, .name {
    font-size: 3.5em;
  }
}

@media screen and (max-width: 300px) {
  .weather-app {
    min-height: 40em;
  }

  body {
    overflow-y: hidden;
  }

  .container > div {
    margin: 0;
    padding: 10px 20px;
  }
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  background-color: #111;
  margin: 0;
  font-family: Raleway, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

h1, h3 {
  font-weight: 400;
}

